<template>
  <div class="config-view">
    <a-row class="item-view" :gutter="[16,16]">
      <a-col :span="24">
        <a-form-item label="">
          <a-input v-model="formData.commonColumns" placeholder="create_time,update_time,create_user_idupdate_user_id,delete_flag" :disabled="disabled"/>
        </a-form-item>
      </a-col>
      <a-col :span="24">
        <a-form-item label="">
          <a-input v-model="formData.hiddenColumns" placeholder="" :disabled="disabled"/>
        </a-form-item>
      </a-col>
      <a-col :span="24">
        <span style="color: rgba(0, 0, 0, 0.85);font-weight: bold">MyBatis-Plus</span>
        <a-popover placement="topLeft">
          <template slot="content">
            <div class="qa-content">
              <div>MyBatis-Plus<span class="code">Mapper</span>MyBatis-Plus</div>
              <div>Insert<span class="code">@TableField(fill = FieldFill.INSERT)</span>insert</div>
              <div>Update<span class="code">@TableField(fill = FieldFill.UPDATE)</span>update</div>
              <div>Insert & Update<span class="code">@TableField(fill = FieldFill.INSERT_UPDATE)</span>insertupdate</div>
              <div><span class="code">@TableLogic</span></div>
            </div>
          </template>
          <a-icon style="margin-left: 10px;cursor: pointer" type="alert" />
        </a-popover>
      </a-col>
      <a-col :span="24">
        <a-form-item label="">
          <a-input v-model="formData.logicDeleteColumns" placeholder="delete_flag" :disabled="disabled"/>
        </a-form-item>
      </a-col>
      <a-col :span="24">
        <a-form-item label="Insert Fill">
          <a-input v-model="formData.insertFillColumns" placeholder="Insertcreate_time,create_user_id" :disabled="disabled"/>
        </a-form-item>
      </a-col>
      <a-col :span="24">
        <a-form-item label="Update Fill">
          <a-input v-model="formData.updateFillColumns" placeholder="Update" :disabled="disabled"/>
        </a-form-item>
      </a-col>
      <a-col :span="24">
        <a-form-item label="insert & Update Fill">
          <a-input v-model="formData.insertOrUpdateFillColumns" placeholder="InsertUpdateupdate_time,update_user_id" :disabled="disabled"/>
        </a-form-item>
      </a-col>
    </a-row>

  </div>
</template>

<script>
import {defaultConfig} from "@/common/default-generator-config";
import ConfigView from "./config-view"
export default {
  extends:ConfigView,
  name: "column-config",
  data(){
    return {
      formData:defaultConfig.columnConfig
    }
  },
  created() {
    this.formData = {...this.config}
  },
  methods:{
    getConfig(){
      return {name: 'columnConfig', config: this.formData};
    }
  },
}
</script>

