<template>
  <a-modal
      :title="datasource.id != null ? '' : ''"
      :visible="visible"
      width="50%"
      okText=""
      cancelText=""
      @cancel="cancel"
      @ok="bindOnSave"
  >
    <div class="edit-form pl-form-line">
      <a-form-item label="">
        <a-input :required="true" v-model="datasource.name" placeholder=""/>
      </a-form-item>
      <a-form-item label="">
        <a-select
            v-model="datasource.dbType"
            style="width: 100%"
            placeholder=""
        >
          <a-select-option
              v-for="item in dbTypes"
              :value="item.id"
              :key="item.id"
              :disabled="item.disable"
          >{{ item.name }}
          </a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="">
        <a-radio-group name="radioGroup" v-model="datasource.connectionType" :default-value="0">
          <a-radio :value="0"></a-radio>
          <a-radio :value="1">URL</a-radio>
        </a-radio-group>
      </a-form-item>
      <div class="connection-host" v-if="datasource.connectionType == '0'">
        <a-form-item label="IP">
          <a-input :required="true" v-model="datasource.host" placeholder="IP"/>
        </a-form-item>
        <a-form-item label="">
          <a-input :required="true" v-model="datasource.port" placeholder=""/>
        </a-form-item>
        <a-form-item label="">
          <a-input :required="true" v-model="datasource.dbName" placeholder=""/>
        </a-form-item>
      </div>

      <div class="connection-url" v-else>
        <a-form-item label="">
          <a-textarea :auto-size="{ minRows: 4, maxRows: 6 }" :required="true" v-model="datasource.url"
                      placeholder=""/>
        </a-form-item>
      </div>

      <a-form-item label="">
        <a-input :required="true" v-model="datasource.username" placeholder=""/>
      </a-form-item>

      <a-form-item label="">
        <a-input type="password" :required="true" v-model="datasource.password" placeholder=""/>
      </a-form-item>
    </div>
  </a-modal>
</template>

<script>

import {add, update} from "@/apis/datasource";
import {message} from "ant-design-vue";

const defalutDatasource = {
  "id": null,
  "name": null,
  "connectionType": 0,
  "username": null,
  "password": null,
  "dbType": 'mysql',
  "host": null,
  "port": '3306',
  "instance": null,
  "dbName": null,
  "url": null
}
const dbTypes = [
  {id: "mysql", name: 'MySQL', disable: false},
  {id: "pg", name: 'pg', disable: true},
  {id: "sqlserver", name: 'SqlServer', disable: true},
  {id: "oracle", name: 'oracle', disable: true},
  {id: "db2", name: 'db2', disable: true}
]
export default {
  name: "edit-form",
  data() {
    return {
      dbTypes,
      visible: false,
      form: this.$form.createForm(this, {name: 'coordinated'}),
      datasource: {...defalutDatasource}
    }
  },
  methods: {
    show(datasource) {
      if (datasource) {
        this.datasource = datasource;
      }
      this.visible = true;
    },
    bindOnSave() {
      const form = {...this.datasource}
      if (form.id) {
        update(form).then(() => {
          this.saveSuccess();
        })
      } else {
        add(form).then(() => {
          this.saveSuccess();
        })
      }
    },
    cancel() {
      this.datasource = {...defalutDatasource}
      this.visible = false;
      this.$emit("change")
    },
    saveSuccess() {
      message.success({
        content: ""
      })
      this.cancel();
    }
  }
}
</script>

<style scoped>

</style>