<template>
  <div class="config-view">
    <a-row class="item-view" :gutter="[16,16]">
      <a-col :span="24">
        <a-form-item label="API">
          <a-switch v-model="formData.basicApi" :disabled="disabled"/>
          <a-popover placement="topLeft">
            <template slot="content">
              <div class="qa-content">
                <div>1.
                  <span class="code">save</span>
                  <span class="code">remove</span>
                  <span class="code">update</span>
                  <span class="code">getById</span>
                  <span class="code">page</span>
                  <span class="code">list</span>
                </div>
              </div>
            </template>
            <a-icon style="margin-left: 10px;cursor: pointer" type="alert"/>
          </a-popover>
        </a-form-item>
      </a-col>

      <a-col :span="24">
        <a-form-item label="">
          <a-popover placement="bottomLeft">
            <template slot="content">
              <div class="qa-content">
                <div>1.<span class="code">PL-WEB</span><span class="code">com.pl.core.response.Result</span>
                </div>
              </div>
            </template>
            <a-input v-model="formData.responseEntity.packageName" placeholder="" :disabled="disabled"/>
          </a-popover>
        </a-form-item>
      </a-col>
      <a-col :span="12">
        <a-form-item label="">
          <a-popover placement="bottomLeft">
            <template slot="content">
              <div class="qa-content">
                <div>1.<span class="code">Result.ok(data)</span><span class="code">ok</span></div>
              </div>
            </template>
            <a-input v-model="formData.responseEntity.successMethod" placeholder="" :disabled="disabled"/>
          </a-popover>
        </a-form-item>
      </a-col>


      <a-col :span="12">
        <a-form-item label="">
          <a-popover placement="bottomLeft">
            <template slot="content">
              <div class="qa-content">
                <div>1.<span class="code">Result.fail(data)</span><span class="code">fail</span></div>
              </div>
            </template>
            <a-input v-model="formData.responseEntity.errorMethod" placeholder="" :disabled="disabled"/>
          </a-popover>
        </a-form-item>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import {defaultConfig} from "@/common/default-generator-config";
import ConfigView from "./config-view"

export default {
  extends:ConfigView,
  name: "controller-config",
  data() {
    return {
      formData: defaultConfig.controllerConfig
    }
  },
  methods: {
    getConfig() {
      return {name: 'controllerConfig', config: this.formData};
    }
  }
}
</script>

<style scoped>

</style>